export const formatDate = (date, formatStr = 'yyyy-MM-dd HH:mm:ss') => {
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'H+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'S+': date.getMilliseconds()
  }
  //date.getFullYear()number,
  if(/(y+)/.test(formatStr)) {
    //+date.getFullYear()+""number
    formatStr = formatStr.replace(RegExp.$1,(date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for(let k in o){
    if(new RegExp('(' + k + ')').test(formatStr)) {
      //String()String(date.getFullYear())
      formatStr = formatStr.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (('00' + o[k]).substr(String(o[k]).length)))
    }
  }

  return formatStr
}